<script>
import ChatItem from 'components/Vjr/ChatItem'
export default {
  name: 'Chat',
  props: ['chatList'],
  components: {
    ChatItem
  },
  data () {
    return {
      myName:'Me',
      newComment:'',
      chatEmoji:false,
      chatMessage:false,
      chatMute:false,
      chatListData:[]
    }
  },
  created: function () {
    this.getChatListData();
  },
  methods: {
    getChatListData: function(){
      var self = this
      self.chatListData=this.chatList
    },
    addComment: function (event) {
      var newCommentObj={}
      var value = this.newComment && this.newComment.trim();
      newCommentObj.name=this.myName;
      newCommentObj.comment=this.newComment;
      newCommentObj.userType="self";
      var currentdate = new Date();
      var datetime = currentdate.getHours() + ":" + currentdate.getMinutes() ;
      newCommentObj.time=datetime;
      this.newComment='';
      this.chatListData.push(newCommentObj);
      setTimeout(function(){
        document.getElementById('chatArea').scrollTop = document.getElementById('chatArea').scrollHeight;
      }, 300);
    },
    openChatEmoji: function (e) {
      var self=this;
      this.chatEmoji=true;
      setTimeout(ClickOutSide, 500);
      function ClickOutSide(e) {
          window.addEventListener("click", dropdownClickOutSide);
      }
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"Layer_ChatEmoji")
        if (!result) {
          window.removeEventListener("click", dropdownClickOutSide);
          self.chatEmoji=false;
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    },
    openChatMessage: function (e) {
      var self=this;
      this.chatMessage=true;
      setTimeout(ClickOutSide, 500);
      function ClickOutSide(e) {
          window.addEventListener("click", dropdownClickOutSide);
      }
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"Layer_ChatMessage")
        if (!result) {
          window.removeEventListener("click", dropdownClickOutSide);
          self.chatMessage=false;
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    },
    openChatMute: function (e) {
      var self=this;
      this.chatMute=true;
      setTimeout(ClickOutSide, 500);
      function ClickOutSide(e) {
          window.addEventListener("click", dropdownClickOutSide);
      }
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"Layer_ChatMute")
        if (!result) {
          window.removeEventListener("click", dropdownClickOutSide);
          self.chatMute=false;
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    }
  }
}
</script>
<template lang="jade">
  .Chat
    .chatArea#chatArea
      ChatItem(v-for="(item, index) in chatListData",:userType="item.userType",:name="item.name",:time="item.time",:comment="item.comment")
    .chatFunc
      div.chatBtn.chatEmoji(v-on:click="openChatEmoji")
        img(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
        .Layer_ChatEmoji(v-if="chatEmoji==true")
          .arrow-bottom1
          .mask-title 表情符號
          .mask-list
            .mask-item
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
              img.left3-img1(src='/static/img/vjr/icon_chatEmoji.png',srcset='/static/img/vjr/icon_chatEmoji@2x.png 2x')
      div.chatBtn.chatMessage(v-on:click="openChatMessage")
        img(src='/static/img/vjr/icon_chatMessage.png',srcset='/static/img/vjr/icon_chatMessage@2x.png 2x')
        .Layer_ChatMessage(v-if="chatMessage==true")
          .arrow-bottom1
          .mask-title 对顾问说
          .mask-list
            .mask-item 不理解，请再说一次
            .mask-item 讲话速度太快
            .mask-item 讲话速度太慢
            .mask-item 请不要仅读课文
            .mask-item 请及时改正我们的错误
            .mask-item 请协助引导学生回答
            .mask-item 请让我们多点时间来练习
            .mask-item 其他学生太多话
      div.chatBtn.chatMute(v-on:click="openChatMute")
        img(src='/static/img/vjr/icon_mute.png',srcset='/static/img/vjr/icon_mute@2x.png 2x')
        .Layer_ChatMute(v-if="chatMute==true")
          .arrow-bottom2
          .mask-title 屏蔽名单
          .mask-list
            .mask-item
              | Lily
              img.closeChatItem(src='/static/img/vjr/icon_close.png',srcset='/static/img/vjr/icon_close@2x.png 2x')
            .mask-item
              | Lucy
              img.closeChatItem(src='/static/img/vjr/icon_close.png',srcset='/static/img/vjr/icon_close@2x.png 2x')
    .newMessage
      span 您对顾问说：请让我们多点时间来练习。
    .comment
      input.commentInput(@keyup.enter="addComment",type='text', value='',v-model="newComment")
      span.sendBtn(v-on:click="addComment")
        img(src='/static/img/vjr/icon_sendBtn.png',srcset='/static/img/vjr/icon_sendBtn@2x.png 2x')
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .Chat {
    @include flex();
    @include flex-width(1);
    flex-direction: column;
    position: relative;
    background: #fff9ee;
    height: 200px;
    width: 100%;
    padding-bottom: 0;
    border-radius: 16px;
    min-height: 50px;
    overflow: hidden;
    .chatFunc{
      position: relative;
      height: 25px;
      background: #FFEFD6;
      padding: 0 10px;
      vertical-align: middle;
      line-height: 35px;
      .chatBtn{
        display: inline-block;
        height: 20px;
        cursor: pointer;
        margin: 0 3px;
      }
      .chatMute{
        float: right;
      }
    }
    .comment{
      @include flex();
      height: 48px;
      padding: 0 10px;
      position: relative;
      .commentInput{
        -webkit-appearance: none;
        box-shadow: none;
        background: transparent;
        width: 120px;
        color: #484848;
        line-height: 24px;
        border: none;
        outline: 0
      }
      .sendBtn {
        height: 25px;
        vertical-align: middle;
        position: absolute;
        right: 10px;
        top: 10px;
        margin-right: 5px;
        cursor: pointer;
      }
    }
    .newMessage {
      position: absolute;
      width: 100%;
      height: auto;
      bottom: 75px;
      left: 0px;
      padding: 5px;
      font-size: 12px;
      background: rgba(0, 0, 0, 0.6);
      color: #fff;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      line-height: 1.5;
    }
    .chatArea {
      position: relative;
      width: 100%;
      @include flex();
      @include flex-width(1);
      flex-direction: column;
      padding: 13px 10px;
      box-sizing: border-box;
      overflow: auto;
    }
    .Layer_ChatEmoji {
      display: block;
      position: absolute;
      bottom: 34px;
      left: 0px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
      .mask-item {
        height: auto;
      }
    }
    .Layer_ChatMessage {
      display: block;
      position: absolute;
      bottom: 34px;
      left: 24px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
    }

    .Layer_ChatMute{
      display: block;
      position: absolute;
      bottom: 34px;
      right: 5px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
    }
    .arrow-top1 {
      width: 0;
      height: 0;
      position: absolute;
      top: -18px;
      left: 10px;
      box-sizing: border-box;
      border-color: transparent;
      border-bottom-color: #fff;
      border-width: 10px;
      border-style: solid;
      cursor: pointer;
    }

    .mask-title {
      line-height: 30px;
      font-size: 12px;
      color: #484848;
      height: 30px;
      text-align: center;
      border-bottom: 1px solid #eee;
    }
    .mask-list {
      padding: 3px 0 10px;
      box-sizing: border-box;
    }
    .mask-item {
      line-height: 25px;
      font-size: 12px;
      color: #484848;
      height: 25px;
      padding: 0 10px;
      box-sizing: border-box;
      cursor: pointer;
      position: relative;
    }

    .mask-item:hover {
      background: #f9f9f9;
    }
    .closeChatItem {
      width: 10px;
      position: absolute;
      right: 10px;
      top: 6px;
    }
  }
</style>



