<script>
export default {
  name: 'CamModal',
  props: [],
  components: {
  },
  methods: {
    hideCam: function () {
      this.$emit('rootHideCamModal')
    }
  }
}
</script>
<template lang="jade">
  .CamModal
    .mask-download-pdf
      .alertIcon
        img(src='/static/img/vjr/alert_icon2.png',srcset='/static/img/vjr/alert_icon2@2x.png 2x')
      .CamModalMessage
        | Jennifer 請你關閉攝影機
      .buttonGroup
        .btn.cancel(v-on:click="hideCam")
          img(src='/static/img/vjr/icon_modalCancelBtn.png',srcset='/static/img/vjr/icon_modalCancelBtn@2x.png 2x')
        .btn.download
          img(src='/static/img/vjr/icon_modalConfirm.png',srcset='/static/img/vjr/icon_modalConfirm@2x.png 2x')

</template>
<style lang='scss' scoped>
@import "../../assets/scss/mixins.scss";
.CamModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 600;
  .mask-download-pdf {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 408px;
    height: 235px;
    background: #fff;
    border: 4px solid #eee;
    border-radius: 16px;
    padding: 60px 0 30px;
    box-sizing: border-box;
  }
  .alertIcon {
    position: absolute;
    text-align: center;
    top: -60px;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
  }
  .CamModalMessage {
    color: #484848;
    font-size: 20px;
    text-align: center;
    line-height: 30px;
  }

  .buttonGroup {
    text-align: center;
    margin: 27px 0 0;
  }
  .btn {
    display: inline-block;;
    width: 140px;
    margin-right: 20px;
    cursor: pointer;
  }
}
</style>



