<script>
export default {
  name: 'ConsultantVideo',
  props: ['zoomSize','consultantVideoSrc'],
  data () {
    return {
      videoIsOpen: true,
      headClose: false,
    }
  },
  methods: {
    clickZoom: function () {
      this.$emit('changeZoomType')
    }
  },
}
</script>
<template lang="jade">
  .ConsultantVideo#ConsultantVideo(:class='{headClose}')
    video(autoplay='autoplay', loop='loop', poster='')
      source(:src="consultantVideoSrc", type='video/mp4')
    .header
      div.ConsultantName Susan Missroon1234567910
      div.VideoIcon
        img.header-i1(v-if="videoIsOpen==true",v-on:click="videoIsOpen=false",src='/static/img/vipabc/icon_ConsultantVideo.png',srcset='/static/img/vipabc/icon_ConsultantVideo@2x.png 2x')
        img.header-i1(v-else-if="videoIsOpen==false",v-on:click="videoIsOpen=true",src='/static/img/vipabc/icon_ConsultantVideoClose.png',srcset='static/img/vipabc/icon_ConsultantVideoClose@2x.png 2x')
      div.headOpen
        img(v-if="headClose==false",v-on:click="headClose=true",src='/static/img/vipabc/icon_headClose.png',srcset='/static/img/vipabc/icon_headClose@2x.png 2x')
        img(v-else-if="headClose==true",v-on:click="headClose=false",src='/static/img/vipabc/icon_headOpen.png',srcset='/static/img/vipabc/icon_headOpen@2x.png 2x')
    .zoomBtn(v-on:click="clickZoom")
      img(v-if="zoomSize!=2",src='/static/img/vipabc/zoomIn.png',srcset='/static/img/vipabc/zoomIn@2x.png 2x')
      img(v-if="zoomSize==2",src='/static/img/vipabc/zoomOut.png',srcset='/static/img/vipabc/zoomOut@2x.png 2x')
</template>

<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .ConsultantVideo{
    position: relative;
    width: 210px;
    height: 210px;
    background: #fff;
    margin-bottom: 11px;
    -webkit-transition: height .3s;
    transition: height .3s;
    box-shadow: 0 0 2px 0px #aaa;
    .header {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 32px;
      @include flex();
      color:#fff;
      background: rgba(247, 76, 76, 0.8);
      line-height: 32px;
      font-size: 12px;
      padding: 0 10px;
      box-sizing: border-box;
    }
    .ConsultantName{
      @include flex();
      @include flex-width(1);
      text-overflow : ellipsis;
      display : inline-block;
      overflow : hidden;
      text-overflow : ellipsis;
      white-space : nowrap;
    }
    .VideoIcon{
      @include flex();
      cursor: pointer;
      width:24px;
      margin: 4px;
    }
    .headOpen{
      @include flex();
      cursor: pointer;
      width:24px;
      margin: 4px;
    }
    .zoomBtn {
      position: absolute;
      cursor: pointer;
      bottom: 6px;
      right: 6px;
    }
    video {
      width: 100%;
      height: 100%;
    }
  }
  .headClose{
    height: 32px !important;
    background: rgba(247, 76, 76, 0.8);
    -webkit-transition: height .3s;
    transition: height .3s;
    .header {
      @include border-radius(16px);
      background: none;
    }
    video {
      display: none;
    }
    .zoomBtn {
      display: none;
    }
  }
</style>



