<script>
import StudentVideoItem from 'components/Vjr/StudentVideoItem'
export default {
  name: 'StudentVideoList',
  props: ['StudentVideoListData'],
  data () {
    return {
      isVideoListClose: false,
    }
  },
  methods: {
    toggleBTNCilck: function () {
      if(this.isVideoListClose==true){
        this.isVideoListClose=false
      }else{
        this.isVideoListClose=true
      }
    }
  },
  components: {
    StudentVideoItem
  }
}
</script>
<template lang="jade">
  .StudentVideoList#StudentVideoList(:class="{ StudentVideoListClose: isVideoListClose }")
    .toggleBTN(v-on:click="toggleBTNCilck")
      img(src='/static/img/vjr/icon_VideoListToggleBtn.png')
    .member-list
      StudentVideoItem(
        v-for="(item, index) in StudentVideoListData",
        :name="item.name",
        :videoSrc="item.videoSrc"
      )
</template>

<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .StudentVideoList {
    height: 160px;
    width: 100%;
    // position: absolute;
    bottom: 0;
    box-sizing: border-box;
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
    overflow: auto;
    overflow-x: auto;
    overflow-y: hidden;
    background: #FFEED0;
    background-image: url('/static/img/vjr/bg_img1.png');
    background-size: 300px 135px;
    background-position: right bottom;
    background-repeat:no-repeat;
    -webkit-transition: bottom .5s ;
    transition: bottom .5s;
  }
  .member-list {
    width: auto;
    height: 100%;
    display: block;
    padding: 10px;
    position: relative;
    display: inline-flex;
  }
  .toggleBTN{
    position: absolute;
    right: 10px;
    z-index: 10;
    cursor: pointer;
  }
  .StudentVideoListClose{
    position: absolute;
    bottom: -136px !important;
    background: none !important;
    background-image:none !important;
    -webkit-transition: bottom .5s background 1s background-image .1s;
    transition: bottom .5s background 1s background-image 1s;
    .member-list {
      display: none;
    }
    .toggleBTN{
      transform:rotate(180deg);
    }
  }
</style>



