<script>
import ChatItem from 'components/Vjr/ChatItem'
export default {
  name: 'ChatForHelper',
  props: ['chatListForHelper'],
  components: {
    ChatItem
  },
  data () {
    return {
      myName:'Me',
      newComment:'',
      chatListData:[]
    }
  },
  created: function () {
    this.getChatListData();
  },
  methods: {
    getChatListData: function(){
      var self = this
      self.chatListData=this.chatListForHelper
    },
    addComment: function (event) {
      var newCommentObj={}
      var value = this.newComment && this.newComment.trim();
      newCommentObj.name=this.myName;
      newCommentObj.comment=this.newComment;
      newCommentObj.userType="self";
      var currentdate = new Date();
      var datetime = currentdate.getHours() + ":" + currentdate.getMinutes() ;
      newCommentObj.time=datetime;
      this.newComment='';
      this.chatListData.push(newCommentObj);
      setTimeout(function(){
        document.getElementById('chatArea').scrollTop = document.getElementById('chatArea').scrollHeight;
      }, 300);
    }
  }
}
</script>
<template lang="jade">
  .Chat
    .chatArea#chatArea
      ChatItem(v-for="(item, index) in chatListData",:userType="item.userType",:name="item.name",:time="item.time",:comment="item.comment")
    .comment
      input.commentInput(@keyup.enter="addComment",type='text', value='',v-model="newComment",placeholder="请在此输入文字")
      span.sendBtn(v-on:click="addComment")
        img(src='/static/img/vjr/icon_sendBtn.png',srcset='/static/img/vjr/icon_sendBtn@2x.png 2x')
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .Chat {
    @include flex();
    @include flex-width(1);
    flex-direction: column;
    position: relative;
    background: #fff;
    height: 300px;
    width: 100%;
    padding-bottom: 0;
    min-height: 50px;
    overflow: hidden;
    .chatFunc{
      position: relative;
      height: 25px;
      background: #FFEFD6;
      padding: 0 10px;
      vertical-align: middle;
      line-height: 35px;
      .chatBtn{
        display: inline-block;
        height: 20px;
        cursor: pointer;
        margin: 0 3px;
      }
      .chatMute{
        float: right;
      }
    }
    .comment{
      @include flex();
      height: 48px;
      padding: 0 10px;
      position: relative;
      border-top: 1px solid #eee;
      .commentInput{
        -webkit-appearance: none;
        box-shadow: none;
        background: transparent;
        width: 120px;
        color: #484848;
        line-height: 24px;
        border: none;
        outline: 0
      }
      .sendBtn {
        height: 25px;
        vertical-align: middle;
        position: absolute;
        right: 10px;
        top: 10px;
        margin-right: 5px;
        cursor: pointer;
      }
    }
    .newMessage {
      position: absolute;
      width: 100%;
      height: auto;
      bottom: 75px;
      left: 0px;
      padding: 5px;
      font-size: 12px;
      background: rgba(0, 0, 0, 0.6);
      color: #fff;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      line-height: 1.5;
    }
    .chatArea {
      position: relative;
      width: 100%;
      @include flex();
      @include flex-width(1);
      flex-direction: column;
      padding: 13px 10px;
      box-sizing: border-box;
      overflow: auto;
    }
    .chatFuncLayer_1 {
      display: none;
      position: absolute;
      bottom: 34px;
      left: 0px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
      .mask-item {
        height: auto;
      }
    }
    .chatFuncLayer_2 {
      display: none;
      position: absolute;
      bottom: 34px;
      left: 24px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
    }

    .chatFuncLayer_3{
      display: none;
      position: absolute;
      bottom: 34px;
      right: 5px;
      width: 160px;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 2;
    }
    .arrow-top1 {
      width: 0;
      height: 0;
      position: absolute;
      top: -18px;
      left: 10px;
      box-sizing: border-box;
      border-color: transparent;
      border-bottom-color: #fff;
      border-width: 10px;
      border-style: solid;
      cursor: pointer;
    }

    .mask-title {
      line-height: 30px;
      font-size: 12px;
      color: #484848;
      height: 30px;
      text-align: center;
      border-bottom: 1px solid #eee;
    }
    .mask-list {
      padding: 3px 0 10px;
      box-sizing: border-box;
    }
    .mask-item {
      line-height: 25px;
      font-size: 12px;
      color: #484848;
      height: 25px;
      padding: 0 10px;
      box-sizing: border-box;
      cursor: pointer;
      position: relative;
    }

    .mask-item:hover {
      background: #f9f9f9;
    }
    .closeChatItem {
      width: 10px;
      position: absolute;
      right: 10px;
      top: 6px;
    }
  }
</style>



