<script>
import WhiteboardHeader from 'components/Vjr/WhiteboardHeader'
import WhiteboardBody from 'components/Vjr/WhiteboardBody'
import StudentVideoList from 'components/Vjr/StudentVideoList'
export default {
  name: 'Whiteboard',
  props: ['StudentVideoListData','MaterialDownloaderShow','CamModalShow'],
  components: {
    WhiteboardHeader,
    WhiteboardBody,
    StudentVideoList
  },
  methods: {
    showMaterial: function () {
      this.$emit('rootShowMaterial')
    },
    showCam: function () {
      this.$emit('rootShowCamModal')
    }
  }
}
</script>
<template lang="jade">
  .Whiteboard
    WhiteboardHeader(v-on:showMaterial="showMaterial",v-on:showCam="showCam",:MaterialDownloaderShow='MaterialDownloaderShow',:CamModalShow='CamModalShow')
    WhiteboardBody
    StudentVideoList(:StudentVideoListData='StudentVideoListData')
</template>
<style lang='scss' scoped>
  @import '../../assets/scss/mixins.scss';
  .Whiteboard {
    @include flex();
    @include flex-width(1);
    flex-direction:column;
    @include border-radius(16px);
    overflow: hidden;
    position: relative;
  }
</style>



