<script>
export default {
  name: 'WhiteboardHeader',
  props: ['MaterialDownloaderShow'],
  components: {
  },
  data () {
    return {
      microphoneIsOpen: true,
      speakerIsOpen: true
    }
  },
  methods: {
    showMaterial: function () {
      this.$emit('showMaterial')
    },
    openDropdown: function (e) {
      let dropdownElement=e.target.parentElement;
      dropdownElement.classList.toggle("show");
      window.addEventListener("click", dropdownClickOutSide);
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"show")
        // console.log(result);
        if (!result) {
          dropdownElement.classList.remove("show");
          window.removeEventListener("click", dropdownClickOutSide);
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    }
  },
}
</script>
