<script>
export default {
  name: 'WhiteboardBody',
  components: {
  }
}
</script>
<template lang="jade">
  #WhiteboardBody
    .tool-box
      .tool-item.active
        img(src='/static/img/vjr/tool1.png',srcset='/static/img/vjr/tool1@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool2.png',srcset='/static/img/vjr/tool2@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool3.png',srcset='/static/img/vjr/tool3@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool4.png',srcset='/static/img/vjr/tool4@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool5.png',srcset='/static/img/vjr/tool5@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool6.png',srcset='/static/img/vjr/tool6@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool7.png',srcset='/static/img/vjr/tool7@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool8.png',srcset='/static/img/vjr/tool8@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool9.png',srcset='/static/img/vjr/tool9@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool10.png',srcset='/static/img/vjr/tool10@2x.png 2x')
      .tool-item
        img(src='/static/img/vjr/tool11.png',srcset='/static/img/vjr/tool11@2x.png 2x')
    .WhiteboardContainer
      div.WhiteboardContainerInner
        img(src='/static/img/vjr/demo/demo.png')
      .pdf-progress
        .progress-loading-box
          img.progress-loading(src='/static/img/vjr/progress_loading.png', style='left: -70%;')
        .progress-logo-box
          img.progress-logo(src='/static/img/vjr/progress_logo.png')
          .progress-text 30%
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  #WhiteboardBody {
    @include flex();
    @include flex-width(1);
    position: relative;
    width: 100%;
    height:40px;
    background: #fff;
    .tool-box{
      background: #FF9E10;
      background-size: contain;
      @include border-radius(8px);
      box-shadow: 0px 3px #E57700;
      width: 40px;
      height: 392px;
      position: absolute;
      top: 10px;
      left: 10px;
      padding: 5px 4px;
      box-sizing: border-box;
      z-index: 100;
    }
    .tool-item{
      width: 32px;
      height: 32px;
      background: #fdb141;
      border-radius: 4px;
      position: relative;
      box-sizing: border-box;
      margin-bottom: 3px;
      cursor: pointer;
    }
    .tool-item.active{
      background: #e97d02;
    }
    .tool-item img{
      width: 24px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    .WhiteboardContainer {
      @include flex();
      @include flex-width(1);
      position: relative;
      padding-left: 60px;
      padding-top: 10px;
      overflow: auto;
    }
    .WhiteboardContainer .pdf-file {
      width: 100%;
      height: 100%;
      display: block;
    }
    .pdf-progress {
      width: 430px;
      height: 20px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
    .progress-loading-box {
      width: 430px;
      height: 20px;
      background: #fff;
      border-radius: 10px;
      border: 2px solid #fff;
      box-sizing: border-box;
      overflow: hidden;
    }
    .progress-loading {
      width: 100%;
      height: 100%;
      display: block;
      position: relative;
    }
    .progress-logo-box {
      width: 75px;
      height: 50px;
      position: absolute;
      right: -60px;
      top: -30px;
    }
    .progress-logo {
      width: 75px;
      height: 50px;
      display: block;
    }
    .progress-text {
      font-size: 12px;
      color: #fff;
      position: absolute;
      bottom: 5px;
      left: 10px;
    }
  }
</style>



