<script>
import ChatItem from 'components/Vjr/ChatItem'
export default {
  name: 'ChatItem',
  props: [
    "userType",
    "name",
    "time",
    "comment"
  ]
}
</script>
<template lang="jade">
  .chatReply(:class="[userType]")
    .replyUser
      span.name(v-if="userType!='host'")  {{ name }}
      span.time(v-if="userType!='host'") {{ time }}
    .replyMsg
      img.icon(v-if="userType=='host'",src='/static/img/vjr/icon_chatMessage.png',srcset='/static/img/vjr/icon_chatMessage@2x.png 2x')
      span.arrow_out(v-if="userType!='host'")
      span.arrow_in(v-if="userType!='host'")
      | {{ comment }}
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .chatReply {
    position: relative;
    .name {
      color: #901500;
      font-size: 13px;
      font-weight: bold;
      margin-right: 5px;

    }
    .time {
      color: #901500;
      font-size: 12px;
    }
  }
  .user {
    .replyMsg {
      position: relative;
      font-size: 12px;
      color: #616161;
      border: 1px solid #ffda6a;
      border-radius: 8px;
      background: #fff;
      padding: 5px 15px;
      box-sizing: border-box;
      max-width: 160px;
      line-height: 1.5;
      margin: 5px 0 10px;
      float: left;
      word-wrap: break-word;
      word-break: break-all;
      .arrow_in{
        width: 0px;
        height: 0px;
        border-width: 6px;
        border-height: 4px;
        border-style: solid;
        border-color: transparent #fff transparent transparent ;
        position: absolute;
        top: 5px;
        left: -10px;
      }
      .arrow_out{
        width: 0px;
        height: 0px;
        border-width: 6px;
        border-height: 4px;
        border-style: solid;
        border-color: transparent #ffda6a transparent transparent ;
        position: absolute;
        top: 5px;
        left: -12px;
      }
    }
  }
  .self{
    .replyUser{
      text-align: right;
    }
    .replyMsg {
      position: relative;
      font-size: 12px;
      color: #fff;
      border: 1px solid #ffda6a;
      border-radius:8px;
      background: #fc9600;
      padding: 5px 15px;
      box-sizing: border-box;
      max-width: 160px;
      /*  left: 10px;*/
      margin: 5px 0 10px;
      float: right;
      word-wrap: break-word;
      word-break: break-all;
      .arrow_in{
        width: 0px;
        height: 0px;
        border-width: 6px;
        border-height: 4px;
        border-style: solid;
        border-color: transparent transparent transparent #fc9600;
        position: absolute;
        top: 5px;
        right: -10px;
      }
      .arrow_out{
        width: 0px;
        height: 0px;
        border-width: 6px;
        border-height: 4px;
        border-style: solid;
        border-color: transparent transparent transparent #ffda6a;
        position: absolute;
        top: 5px;
        right: -12px;
      }
    }
  }
  .host{
    color: #484848;
    font-size: 12px;
    position: relative;
    padding: 5px 10px 5px 30px;
    box-sizing: border-box;

    .replyUser{
      text-align: left;
    }
    .replyMsg {
      position: relative;
      font-size: 12px;
      color: #616161;
      padding: 0;
      border: none;
      box-sizing: border-box;
      background: none;
      max-width: 100%;
      word-wrap: break-word;
      word-break: break-all;
      /*  left: 10px;*/
      margin: 0;
      float: left;
      .icon{
        position: absolute;
        left: -30px;
      }
    }
  }

</style>



