<script>
import Vjr from 'components/Vjr/Vjr'
import Vipabc from 'components/Vipabc/Vipabc'
import $ from 'jquery'
export default {
  name: 'app',
  data () {
    return {
      roomType: '',
      roomNum:'',
      memberList:[],
      chatList:[],
      consultantVideoSrc:'',
      StudentVideoListData:[],
      chatListForHelper:[],
      playeRecordVideo: false
    }
  },
  components: {
    Vipabc,
    Vjr
  },
  created: function () {
    this.compiled()
  },
  methods: {
    compiled: function(){
      var self = this
      $.ajax({
        url: '/static/data/consoleData.json',
        complete: function (data) {
        self.roomType = data.responseJSON.roomType
        self.roomNum = data.responseJSON.roomNum
        self.memberList = data.responseJSON.memberList
        self.chatList = data.responseJSON.chatList
        self.consultantVideoSrc = data.responseJSON.consultantVideoSrc
        self.StudentVideoListData = data.responseJSON.StudentVideoListData
        self.chatListForHelper = data.responseJSON.chatListForHelper
        self.playeRecordVideo = data.responseJSON.playeRecordVideo
        }
      })
    }
  }
}
</script>
<template lang="jade">
  #app(v-if="roomType==1")
    Vjr(:roomNum="roomNum",:memberList="memberList",:chatList="chatList",:consultantVideoSrc="consultantVideoSrc",:StudentVideoListData="StudentVideoListData",:chatListForHelper="chatListForHelper",:playeRecordVideo="playeRecordVideo")
  #app(v-else-if="roomType==2")
    Vipabc(:roomNum="roomNum",:memberList="memberList",:chatList="chatList",:consultantVideoSrc="consultantVideoSrc",:StudentVideoListData="StudentVideoListData",:chatListForHelper="chatListForHelper",:playeRecordVideo="playeRecordVideo")
</template>
<style lang='scss'>
  @import "/assets/scss/video-js.scss";
  @import "/assets/scss/video-js_style.scss";
</style>
<style lang='scss' scoped>
  @import "/assets/scss/mixins.scss";
  #app{
    @include flex();
    @include flex-width(1);
    flex-direction:column;
    width: 100%;
    height:100%;
    min-width: 1024px !important;
    min-height: 650px !important;
  }
</style>



