    .poll-content
      .poll-question What's your faverite exercise?
      ul.poll-content
        li.poll-button A basketball
        li.poll-button B Swimming
        li.poll-button C baseball
        li.poll-button D running
      .poll-btnWarp
        img.poll-btnimg(src='/static/img/vjr/icon_sendBtn.png',srcset='/static/img/vjr/icon_sendBtn@2x.png 2x')

