    font-size: 13px;
    font-weight: normal;
    text-align: right;
    padding: 0 10px;
    line-height: 25px;
    width: 33.333333%;
  }

  .poll-td1,
  .poll-td2 {
    font-size: 13px;
    font-weight: normal;
    text-align: left;
    padding: 0 10px;
    line-height: 25px;
