  }
  .poll-btn {
    height: 25px;
    vertical-align: middle;
    margin-right: 5px;
    cursor: pointer;
  }
  .poll-button{
    text-align: left;
    height: 35px;
    line-height:40px;
    vertical-align: middle;
    margin-right: 5px;
