<script>
export default {
  name: 'MemberTableItem',
  props: ['name','headsetVolume','percentage','flagOpen','microphoneOpen','penOpen','ITOpen'],
  data () {
    return {
      value: 0,
      isFlagOpen:true,
      isMicrophoneOpen:true,
      isPenOpen:true,
      isITOpen:true
    }
  },
  created: function () {
    this.setValue();
  },
  methods: {
    setValue: function(){
      var self = this
      self.value=this.headsetVolume
      self.isFlagOpen=this.flagOpen
      self.isMicrophoneOpen=this.microphoneOpen
      self.isPenOpen=this.penOpen
      self.isITOpen=this.ITOpen
    }
  }
}
</script>
<template lang="jade">
  tr
    td {{ percentage }}
    td.nameTD
      .name {{ name }}
    td
      span.memberIconBtn
        img(v-if="isFlagOpen==true",v-on:click="isFlagOpen=false",src='/static/img/vipabc/icon_flagOpen.png',srcset='/static/img/vipabc/icon_flagOpen@2x.png 2x')
        img(v-if="isFlagOpen==false",v-on:click="isFlagOpen=true",src='/static/img/vipabc/icon_flagClose.png',srcset='/static/img/vipabc/icon_flagClose@2x.png 2x')
    td
      span.memberIconBtn
        img(v-if="isMicrophoneOpen==true",v-on:click="isMicrophoneOpen=false",src='/static/img/vipabc/icon_MicOpen.png',srcset='/static/img/vipabc/icon_MicOpen@2x.png 2x')
        img(v-if="isMicrophoneOpen==false",v-on:click="isMicrophoneOpen=true",src='/static/img/vipabc/icon_MicClose.png',srcset='/static/img/vipabc/icon_MicClose@2x.png 2x')
    td
      span.memberIconBtn
        img(v-if="isPenOpen==true",v-on:click="isPenOpen=false",src='/static/img/vipabc/icon_penOpen.png',srcset='/static/img/vipabc/icon_penOpen@2x.png 2x')
        img(v-if="isPenOpen==false",v-on:click="isPenOpen=true",src='/static/img/vipabc/icon_penClose.png',srcset='/static/img/vipabc/icon_penClose@2x.png 2x')
    td
      span.memberIconBtn
        img(v-if="isITOpen==true",v-on:click="isITOpen=false",src='/static/img/vipabc/icon_ITOpen.png',srcset='/static/img/vipabc/icon_ITOpen@2x.png 2x')
        img(v-if="isITOpen==false",v-on:click="isITOpen=true",src='/static/img/vipabc/icon_ITClose.png',srcset='/static/img/vipabc/icon_ITClose@2x.png 2x')

</template>
<style lang='scss' scoped>
 td{
  vertical-align:middle;
  text-align: center;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
 }
 .nameTD{
  width: 100px;
  padding: 0 5px;
 }
 .name{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
 }
 .memberIconBtn{
   cursor: pointer;
 }
</style>



