<script>
import ChatForHelper from 'components/Vjr/ChatForHelper'
export default {
  name: 'HelperMessage',
  props: ['chatListForHelper'],
  components: {
    ChatForHelper
  },
  data () {
    return {
      helperMessageListShow:false,
      hostMessageListShow:false,
    }
  },
  methods: {
    hostMessageSwitch: function () {
      if(this.hostMessageListShow==true){
        this.hostMessageListShow=false;
      }else{
        this.hostMessageListShow=true;
      }
    }
  }
}
</script>
<template lang="jade">
  .helperMessage
    .helperMessageBtn(v-on:click="helperMessageListShow=true")
      img(src='/static/img/vjr/helperMessageBtn.png')
      .helperMessageRecord 1
    .helperMessageList(v-if="helperMessageListShow")
      .hostMessageContent
        .hostMessageContentTitle
          | 小帮手信息
          .btn.closeHelperMessageList(v-on:click="helperMessageListShow=false")
            img(src='/static/img/vjr/icon_modalCloseBtn.png',srcset='/static/img/vjr/icon_modalCloseBtn@2x.png 2x')
        .hostMessageItem.center【5条小帮手】处理中
        .hostMessageList(v-if="hostMessageListShow")
          .hostMessageItem
            .undone
            | 【看不到教材】问题已为您处理，请问是否有改善？
            .message-answer
              img.a1(src='/static/img/vjr/icon_messageBtn_1.png',srcset='/static/img/vjr/icon_messageBtn_1@2x.png 2x')
              img.a2(src='/static/img/vjr/icon_messageBtn_2.png',srcset='/static/img/vjr/icon_messageBtn_3@2x.png 2x')
              img.a3(src='/static/img/vjr/icon_messageBtn_3.png',srcset='/static/img/vjr/icon_messageBtn_3@2x.png 2x')
          .hostMessageItem
            .done
            | 【通讯有杂音】问题已为您处理完成， 感谢您的反馈。
          .hostMessageItem
            .done
            | 【通讯有杂音】问题已为您处理完成， 感谢您的反馈。
          .hostMessageItem
            .done
            | 【通讯有杂音】问题已为您处理完成， 感谢您的反馈。
          .hostMessageItem
            .done
            | 【通讯有杂音】问题已为您处理完成， 感谢您的反馈。
        .hostMessageSwitch(v-on:click="hostMessageSwitch")
          img(v-if="hostMessageListShow==false", src='/static/img/vjr/icon_messageArrowDown.png',srcset='/static/img/vjr/icon_messageArrowDown@2x.png 2x')
          img(v-else-if="hostMessageListShow==true", src='/static/img/vjr/icon_messageArrowUp.png',srcset='/static/img/vjr/icon_messageArrowUp@2x.png 2x')
      ChatForHelper(:chatListForHelper="chatListForHelper")
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .helperMessageBtn {
    width: 60px;
    height: 60px;
    position: absolute;
    right: 30px;
    top: 120px;
    cursor: pointer;
    .helpermessage-icon {
      width: 100%;
      height: 100%;
      display: block;
    }
    .helperMessageRecord {
      height: 24px;
      width: 24px;
      position: absolute;
      top: 0px;
      right: 0;
      font-size: 14px;
      color: #fff;
      text-align: center;
      background: #F74C4C;
      border-radius: 50%;
      line-height: 24px;
    }
  }
  .helperMessageList {
    width: 210px;
    position: absolute;
    right: 20px;
    top: 120px;
    border-radius: 4px;
    box-shadow: 0px 0px 4px 1px #cecdcd;
    .closeHelperMessageList {
      width: 14px;
      position: absolute;
      right: 15px;
      top: 0px;
      cursor: pointer;
    }
    .hostMessageContentTitle {
      height: 42px;
      font-size: 14px;
      color: #484848;
      font-weight: bold;
      text-align: center;
      line-height: 42px;
    }

    .hostMessageContent {
      border-radius: 4px;
      background: #FAFAFA;
      .hostMessageList{
        max-height: 150px;
        overflow: auto;
      }
      .hostMessageItem{
        border-top: 1px solid #eee;
        background: #fff;
        line-height: 18px;
        padding: 5px 5px 5px 25px;
        box-sizing: border-box;
        color: #646464;
        font-size: 12px;
        position: relative;
      }
      .center {
        padding: 5px 5px;
        text-align: center;
      }
      .helpermessage-item-i {
        position: absolute;
        top: 10px;
        left: 10px;
        width: 10px;
        display: none;
      }
      .undone {
        position: absolute;
        top: 10px;
        left: 10px;
        display: inline-block;
        width: 10px;
        height: 10px;
        background: #F74C4C;
        border-radius: 20px;
      }
      .done {
        position: absolute;
        top: 10px;
        left: 10px;
        display: inline-block;
        width: 10px;
        height: 10px;
        background: #68AF00;
        border-radius: 20px;
      }
      .hostMessageSwitch {
        height: 20px;
        box-shadow: 0px 0px 4px 1px #cecdcd;
        text-align: center;
      }
    }
  }
</style>



