<script>
import HeaderInner from 'components/Vipabc/HeaderInner'
import ConsultantVideo from 'components/Vipabc/ConsultantVideo'
import MemberList from 'components/Vipabc/MemberList'
import Chat from 'components/Vipabc/Chat'
import Whiteboard from 'components/Vipabc/Whiteboard'
import HelperMessage from 'components/Vipabc/HelperMessage'
import MaterialDownloadModal from 'components/Vipabc/MaterialDownloadModal'
import PollModal from 'components/Vipabc/PollModal'
import RecordPlayer from 'components/Vipabc/RecordPlayer'
export default {
  name: 'Vipabc',
  props: ['roomNum','memberList','chatList','consultantVideoSrc','StudentVideoListData','chatListForHelper','playeRecordVideo'],
  components: {
    HeaderInner,
    ConsultantVideo,
    MemberList,
    Chat,
    Whiteboard,
    HelperMessage,
    MaterialDownloadModal,
    PollModal,
    RecordPlayer
  },
  data () {
    return {
      videoIsOpen: true,
      zoomSize: 0,
      leftToggleClose:false,
      zoomStyle: {
        Zoom2x: false,
        Zoom3x: false
      },
      MaterialDownloaderShow:false
    }
  },
  methods: {
    zoomType: function () {
      this.zoomSize+=1;
      if(this.zoomSize%3==0){
        this.zoomSize=0;
        this.zoomStyle.Zoom2x=false
        this.zoomStyle.Zoom3x=false
      }else if(this.zoomSize%3==1){
        this.zoomStyle.Zoom2x=true
        this.zoomStyle.Zoom3x=false
      }else if(this.zoomSize%3==2){
        this.zoomStyle.Zoom2x=false
        this.zoomStyle.Zoom3x=true
      }
    },
    rootShowMaterial: function () {
      this.MaterialDownloaderShow=true;
    },
    rootHideMaterial: function () {
      this.MaterialDownloaderShow=false;
    },
    setLeftToggle: function () {
      if(this.leftToggleClose==true){
        this.leftToggleClose=false;
      }else{
        this.leftToggleClose=true;
      }
    }
  }
}
</script>
<template lang="jade">
  #TutorMeet(:class="zoomStyle")
    #header(class='header')
      HeaderInner(:roomNum='roomNum')
    #content.content(:class="{contentLeftPadding0: leftToggleClose }")
      #TutorMeetSideBar.TutorMeetSideBar(:class="{ TutorMeetSideBarClose: leftToggleClose }")
        ConsultantVideo(v-on:changeZoomType="zoomType",:zoomSize='zoomSize',:consultantVideoSrc="consultantVideoSrc")
        MemberList(v-if="playeRecordVideo==false",:memberList="memberList")
        Chat(:chatList="chatList")
      #TutorMeetToggleBtn.TutorMeetToggleBtn(v-on:click="setLeftToggle")
        img(v-if="leftToggleClose==false",src='/static/img/vipabc/TutorMeetToggleBtnLeft.png',srcset='/static/img/vipabc/TutorMeetToggleBtnLeft@2x.png 2x')
        img(v-if="leftToggleClose==true",src='/static/img/vipabc/TutorMeetToggleBtnRight.png',srcset='/static/img/vipabc/TutorMeetToggleBtnRight@2x.png 2x')
      #WhiteboardWrap.WhiteboardWrap
        Whiteboard(v-if="playeRecordVideo==false",:StudentVideoListData="StudentVideoListData",:MaterialDownloaderShow="MaterialDownloaderShow",v-on:rootShowMaterial="rootShowMaterial")
        RecordPlayer(v-if="playeRecordVideo==true")
    HelperMessage(:chatListForHelper="chatListForHelper")
    MaterialDownloadModal(v-if="MaterialDownloaderShow==true",v-on:rootHideMaterial="rootHideMaterial"))
    PollModal
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  #TutorMeet {
    @include flex();
    @include flex-width(1);
    flex-direction:column;
    width: 100%;
    height:100%;
    min-width: 1024px !important;
    min-height: 650px !important;
    background: #E6E6E6;
    .header{
      width:100%;
      height: 66px;
      @include flex;
    }
    .content{
      @include flex;
      @include flex-width(1);
      padding: 0 13px 13px 13px;
      overflow: auto;
    }
    .contentLeftPadding0{
      padding-left: 0;
    }
    .TutorMeetSideBar{
      @include flex;
      flex-direction:column;
      width: 210px;
      overflow: auto;
    }
    .TutorMeetSideBarClose{
      display: none !important;
    }
    .TutorMeetToggleBtn{
      @include flex;
      @include align-center;
      @include justify-center;
      width: 14px;
      cursor: pointer;
    }
    .WhiteboardWrap{
      @include flex;
      @include flex-width(1);
      overflow: auto;
      box-shadow: 0 0 2px 0px #aaa;
    }
  }
  .Zoom2x{
    #TutorMeetSideBar{
      @include flex;
      flex-direction:column;
      width: 410px;
      overflow: auto;
    }
    #ConsultantVideo{
      width: 410px;
      height: 410px;
    }
  }
  .Zoom3x{
    #TutorMeetSideBar{
      @include flex;
      flex-direction:column;
      width: 510px;
      overflow: auto;
    }
    #ConsultantVideo{
      width: 510px;
      height: 510px;
    }
  }
</style>



