<script>
export default {
  name: 'WhiteboardHeader',
  props: ['MaterialDownloaderShow','CamModalShow'],
  components: {
  },
  data () {
    return {
      microphoneIsOpen: true,
      speakerIsOpen: true
    }
  },
  methods: {
    showMaterial: function () {
      this.$emit('showMaterial')
    },
    showCam: function () {
      this.$emit('showCam')
    },
    openDropdown: function (e) {
      let dropdownElement=e.target.parentElement;
      dropdownElement.classList.toggle("show");
      window.addEventListener("click", dropdownClickOutSide);
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"show")
        // console.log(result);
        if (!result) {
          dropdownElement.classList.remove("show");
          window.removeEventListener("click", dropdownClickOutSide);
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    }
  },
}
</script>
<template lang="jade">
  #WhiteboardHeader
    .buttonGroup.group_1
      .btn(v-on:click="showMaterial")
        img(src='/static/img/vjr/icon_materialDownloadBtn.png',srcset='/static/img/vjr/icon_materialDownloadBtn@2x.png 2x')
      .btn(v-on:click="showCam")
        img(src='/static/img/vjr/icon_materials_DownloadBtn.png',srcset='/static/img/vjr/icon_materials_DownloadBtn@2x.png 2x')
    .buttonGroup.group_2
      .btn
        img(src='/static/img/vjr/icon_firstPage.png',srcset='/static/img/vjr/icon_firstPage@2x.png 2x')
      .btn
        img(src='/static/img/vjr/icon_prevPage.png',srcset='/static/img/vjr/icon_prevPage@2x.png 2x')
      .dropdown(v-on:click="openDropdown")
        .dropdownBtn
        .pageNum 1
        ul.dropdownMenu
          li 1
          li.active 2
          li 3
      .btn
        img(src='/static/img/vjr/icon_nextPage.png',srcset='/static/img/vjr/icon_nextPage@2x.png 2x')
      .btn
        img(src='/static/img/vjr/icon_lastPage.png',srcset='/static/img/vjr/icon_lastPage@2x.png 2x')
      .dropdown(v-on:click="openDropdown")
        .dropdownBtn
        .btn
          img(src='/static/img/vjr/icon_search.png',srcset='/static/img/vjr/icon_search@2x.png 2x')
        ul.dropdownMenu
          li 50%
          li 75%
          li.active 100%
          li 125%
          li 150%
    .buttonGroup.group_3
      .btn
        img(v-if="microphoneIsOpen==true",v-on:click="microphoneIsOpen=false",src='/static/img/vjr/icon_microphone.png',srcset='/static/img/vjr/icon_microphone@2x.png 2x')
        img(v-if="microphoneIsOpen==false",v-on:click="microphoneIsOpen=true",src='/static/img/vjr/icon_microphone_close.png',srcset='/static/img/vjr/icon_microphone_close@2x.png 2x')
      .btn
        img(v-if="speakerIsOpen==true",v-on:click="speakerIsOpen=false",src='/static/img/vjr/icon_speaker.png',srcset='/static/img/vjr/icon_speaker@2x.png 2x')
        img(v-if="speakerIsOpen==false",v-on:click="speakerIsOpen=true",src='/static/img/vjr/icon_speaker_close.png',srcset='/static/img/vjr/icon_speaker_close@2x.png 2x')
    .buttonGroup.group_4
      .btn
        img(src='/static/img/vjr/icon_microphone.png',srcset='/static/img/vjr/icon_handup@2x.png 2x')
      .btn
        img(src='/static/img/vjr/icon_thumbsUp.png',srcset='/static/img/vjr/icon_thumbsUp@2x.png 2x')
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  #WhiteboardHeader {
    @include flex();
    position: relative;
    width: 100%;
    height:40px;
    background: #F1293A;
    vertical-align:middle;
    line-height: 40px;
    padding: 0 10px;
    display: inline-block;
    .buttonGroup{
      display: block;
      float: left;
      position: relative;
      height: 40px;
      line-height: 40px;
      padding: 0 10px;
      .btn{
        display: inline-block;
        width:25px;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
        margin: 0 10px;
        &:hover{
          opacity:0.8;
        }
        img{
          vertical-align: middle;
        }
      }
    }
    .group_1 {
      border-right: 1px solid #f94f4f;
    }
    .group_2 {
    }
    .group_3{
      float: right;
      border-left: 1px solid #f94f4f;
    }
    .group_4{
      float: right;
      border-left: 1px solid #f94f4f;
    }
    .dropdown{
      display: inline-block;
      position: relative;
      margin-right: 10px;
    }
    .dropdownBtn{
      position: absolute;
      cursor: pointer;
      display: block;
      vertical-align: middle;
      width: 100%;
      height: 100%;
      background: rgba(255,255,2550,0);
      z-index: 2;
    }
    .dropdownIcon{
      display: inline-block;;
      vertical-align: middle;
      text-align: left;
      font-size: 14px;
      height: 28px;
      line-height: 26px;
      border-radius: 28px;
      padding: 0 40px;
      background: #F1293B;
      color: #fff;
      border: 2px solid #FF6B6B;
      z-index: 1;
    }
    .dropdownMenu{
      position: absolute;
      top: 50px;
      left: 0px;
      color: #333;
      width: auto;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 9999;
      display: none;
      min-width: 40px;
      .dropdownMenuTitle{
        width: 100%;
        border-bottom: 1px solid #ededed;
        padding: 10px;
        text-align: center;
      }
      .dropdownMenuArrow {
        width: 0;
        height: 0;
        position: absolute;
        top: -12px;
        left: 0;
        right: 0;
        margin: auto;
        padding: 0;
        box-sizing: border-box;
        border-color: transparent;
        border-bottom-color: #fff;
        border-width: 6px;
        border-style: solid;
        cursor: pointer;
      }
      li{
        font-size: 12px;
        padding: 0px 6px;
        text-align: center;
        &:hover{
          background: #eee;
          > .submenu{
            display: block;
          }
        }
      }
      .active{
        color: #f00;
      }
    }
    .show{
      .dropdownMenu{
        display: block !important;
      }
    }
    .pageNum{
      display: inline-block;
      color: #fff;
      background: #d20000;
      line-height: 20px;
      padding: 2px 16px;
      box-sizing: border-box;
      vertical-align: middle;
      border-radius: 12px;
      cursor: pointer;
    }
    .volumeRange{
      position: relative;
      width: 100%;
      height:155px;
      text-align: center;
      padding: 10px 0;
    }
    .volumeIcon{
      position: relative;
      border-top: 1px solid #eee;
      width: 100%;
      height: 50px;
      line-height: 50px;
      padding: 5px 0;
      text-align: center;
    }
  }

  input[type=range] {
    -webkit-appearance: none;
    // -webkit-appearance: slider-vertical;
    -webkit-transform:rotate(270deg);
    -moz-transform:rotate(270deg);
    -o-transform:rotate(270deg);
    -ms-transform:rotate(270deg);
    transform:rotate(270deg);
    transform-origin:0 0;
    position: absolute;
    top:140px;
    left:18px;
    margin: auto;
    outline: none;
    padding: 0;
    height: 4px;
    width: 130px;
    background-color: #eee;
    background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, #f74c4c), color-stop(100%, #f74c4c));
    background-image: -webkit-linear-gradient(#f74c4c, #f74c4c);
    background-image: -moz-linear-gradient(#f74c4c, #f74c4c);
    background-image: -o-linear-gradient(#f74c4c, #f74c4c);
    background-image: linear-gradient(#f74c4c, #f74c4c);
    background-size: 50% 100%;
    background-repeat: no-repeat;
    border-radius: 10px;
    cursor: pointer;

  }

  input[type=range]::-webkit-slider-runnable-track {
      box-shadow: none;
      border: none;
      background: transparent;
      -webkit-appearance: none;
  }

  input[type=range]::-moz-range-track {
      box-shadow: none;
      border: none;
      background: transparent;
  }

  input[type=range]::-moz-focus-outer {
      border: 0;
  }

  input[type=range]::-webkit-slider-thumb {
      width: 10px;
      height: 10px;
      border: 0;
      background: #f74c4c;
      border-radius: 100%;
      box-shadow: 0 0 1px 0px rgba(0,0,0,0.1);
      -webkit-appearance: none;
  }

  input[type=range]::-moz-range-thumb {
      width: 14px;
      height: 14px;
      border: 0;
      background: #f74c4c;
      border-radius: 100%;
      box-shadow: 0 0 1px 0px rgba(0,0,0,0.1);
  }
</style>



