<script>
import MemberTableItem from 'components/Vipabc/MemberTableItem'
export default {
  name: 'MemberList',
  props: ['memberList'],
  data () {
    return {
      headClose: false,
    }
  },
  components: {
    MemberTableItem,
  }
}
</script>
<template lang="jade">
  .MemberList#MemberList(:class='{headClose}')
    .header
      div.headerTitle Online Members
      div.headOpen
        img(v-if="headClose==false",v-on:click="headClose=true",src='/static/img/vjr/icon_headClose.png',srcset='/static/img/vjr/icon_headClose@2x.png 2x')
        img(v-else-if="headClose==true",v-on:click="headClose=false",src='/static/img/vjr/icon_headOpen.png',srcset='/static/img/vjr/icon_headOpen@2x.png 2x')
    .memberTable
      table.memberTableList
        thead
          th.memberPercentage
          th.memberName 
          th.memberFlag
            img.i1(src='/static/img/vipabc/icon_flagTitleIcon.png',srcset='/static/img/vipabc/icon_flagTitleIcon@2x.png 2x')
          th.memberMic
            img.i1(src='/static/img/vipabc/icon_MicTitleIcon.png',srcset='/static/img/vipabc/icon_MicTitleIcon@2x.png 2x')
          th.memberPen
            img.i1(src='/static/img/vipabc/icon_penTitleIcon.png',srcset='/static/img/vipabc/icon_penTitleIcon@2x.png 2x')
          th.memberIT
            img.i1(src='/static/img/vipabc/icon_ITTitleIcon.png',srcset='/static/img/vipabc/icon_ITTitleIcon@2x.png 2x')
        tbody
          MemberTableItem(
            v-for="(item, index) in memberList",
            :name="item.name",
            :headsetVolume="item.headsetVolume",
            :percentage="item.percentage",
            :flagOpen="item.flagOpen",
            :microphoneOpen="item.microphoneOpen",
            :penOpen="item.penOpen",
            :ITOpen="item.ITOpen"
          )
    .member_resize_btn
      img.adjust1-img(src='/static/img/vipabc/icon_twoline.png',srcset='/static/img/vipabc/icon_twoline@2x.png 2x')
</template>
<style lang='scss'>
//全域css類別
.ui-resizable-handle{
  position: absolute;;
  width: 100%;
  height: 11px;
  cursor: ns-resize;
  bottom: -12px;
}
</style>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .MemberList {
    position: relative;
    background: #fff;
    height: 200px;
    width: 100%;
    padding-bottom: 0;
    margin-bottom: 13px;
    box-shadow: 0 0 2px 0px #aaa;
    @include flex();
    flex-direction:column;
    -webkit-transition: height .3s;
    transition: height .3s;
    .header {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 32px;
      @include flex();
      @include border-top-radius(16px);
      line-height: 32px;
      font-size: 12px;
      padding: 0 10px;
      box-sizing: border-box;
      color: #666666;
      background: #fff;
      z-index: 100;
    }
    .headerTitle {
      @include flex();
      @include flex-width(1);
      text-overflow : ellipsis;
      display : inline-block;
      overflow : hidden;
      text-overflow : ellipsis;
      white-space : nowrap;
    }
    .headOpen{
      @include flex();
      cursor: pointer;
      width:24px;
      margin: 4px;
    }
    .memberTable{
      @include flex();
      @include flex-width(1);
      overflow: auto;
      margin-top: 32px;
      @include border-radius(16px);
      thead{
        border-top: 1px solid #eee;
      }
    }
    .member_resize_btn{
      position: absolute;
      width: 100%;
      height: 13px;
      bottom: -12px;
      text-align: center;
      vertical-align: top;
      line-height: 11px;
      img{

      }
    }
  }
  .memberTableList{
    position: relative;
    width: 100%;
    table-layout: fixed;
    border-bottom: 1px solid #EEEEEE;
    font-size: 12px;
    color: #666;
    th{
      vertical-align: middle;
      border-left: 1px solid #EEEEEE;
      border-right: 1px solid #EEEEEE;
    }
    tr {
      height: 26px;
      border-top: 1px solid #EEEEEE;
    }
    tr:nth-child(odd) {
      background: #F8F8F8;
    }
    td {
      border-left: 1px solid #EEEEEE;
      border-right: 1px solid #EEEEEE;
      vertical-align: middle;
    }
    .memberPercentage{
      width: 20%;
      padding: 0 10px;
      box-sizing: border-box;
      text-align: left;
    }
    .memberName{
      width: 30%;
      padding: 0 10px;
      box-sizing: border-box;
      text-align: left;
      .memberNameDiv{
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
    .memberFlag{
      width: 15%;
      text-align: center;
    }
    .memberMic{
      width: 15%;
      text-align: center;
    }
    .memberPen{
      width: 15%;
      text-align: center;
    }
    .memberIT{
      width: 15%;
      text-align: center;
    }

  }
  .headClose{
    height: 32px !important;
    background: #fff;
    -webkit-transition: height .3s;
    transition: height .3s;
    .header {
      @include border-radius(16px);
      background: none;
    }
    video {
      display: none;
    }
    .zoomBtn {
      display: none;
    }
    .memberTableList{
      display: none;
    }
  }
</style>



