<script>
export default {
  name: 'StudentVideoItem',
  props: ['name','videoSrc'],
  data () {
    return {
      videoIsOpen: true,
    }
  },
  components: {
  }
}
</script>
<template lang="jade">
  .member-item
    .video
      video(autoplay='autoplay', loop='loop', poster='',v-if="videoIsOpen==true")
        source(:src='videoSrc', type='video/mp4')
    .member-item-footer
      span {{name}}
      .videoBtn
        img(src='/static/img/vjr/icon_studentVideoOpen.png',srcset='/static/img/vjr/icon_studentVideoOpen@2x.png 2x',v-if="videoIsOpen==true",v-on:click="videoIsOpen=false")
        img(src='/static/img/vjr/icon_studentVideoClose.png',srcset='/static/img/vjr/icon_studentVideoClose@2x.png 2x',v-if="videoIsOpen==false",v-on:click="videoIsOpen=true")
</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";

  .member-item {
    width: 200px;
    height: 130px;
    border-radius: 10px;
    background: #fff;
    position: relative;
    margin-right: 10px;
    display: inline-block;
    float: left;
  }

  .video {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    video{
      width: 100%;
      height: 100%;
    }
  }

  .member-item-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 32px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background: rgba(255, 255, 255, 0.8);
    line-height: 32px;
    font-size: 12px;
    padding: 0 10px;
    box-sizing: border-box;
  }

  .videoBtn {
    width: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
  }
</style>



