<script>
export default {
  name: 'HeaderInner',
  props: ['roomNum'],
  data () {
    return {
    }
  },
  methods: {
    openDropdown: function (e) {
      let dropdownElement=e.target.parentElement;
      dropdownElement.classList.toggle("show");
      window.addEventListener("click", dropdownClickOutSide);
      function dropdownClickOutSide(e) {
        var result=hasClass(e.target ,"show")
        // console.log(result);
        if (!result) {
          dropdownElement.classList.remove("show");
          window.removeEventListener("click", dropdownClickOutSide);
        }
      }
      function hasClass(element, className) {
        var regex = new RegExp('\\b' + className + '\\b');
        do {
          if (regex.exec(element.className)) {
            return true;
          }
          element = element.parentNode;
        } while (element);
        return false;
      }
    }
  }
}
</script>
<template lang="jade">
  .headerInner
    .headerLogo
      img(src='/static/img/vipabc/vipabc_logo_white.png',srcset='/static/img/vipabc/vipabc_logo_white@2x.png 2x')
    .headerBtnGroup
      .dropdown(v-on:click="openDropdown")
        .dropdownBtn
        div
          img(src='/static/img/vipabc/headerBtn_1.png',srcset='/static/img/vipabc/headerBtn_1@2x.png 2x')
        ul.dropdownMenu
          .dropdownMenuArrow
          .dropdownMenuTitle 我需要帮助
          li 通讯有杂音
          li 通讯有延迟
          li 顾问不在教室
          li 顾问听不到我的声音
          li 听不到顾问声音
          li 听不到同学声音
          li 听顾问太小声
          li 看不到教材
          li 其他问题（找IT）
            img.i-arrow(src='/static/img/vipabc/icon_arrowForSubMenu.png',srcset='/static/img/vipabc/icon_arrowForSubMenu@2x.png 2x')
            ul.submenu.left
              .dropdownMenuTitle 请IT人员立刻与我联系以下问题：
              textarea.headerTextarea(placeholder='请在此输入文字')
              .sendBtn
                img.poll-btnimg(src='/static/img/vipabc/icon_sendBtn.png',srcset='/static/img/vipabc/icon_sendBtn@2x.png 2x')
          li 请课后与我联系
      .dropdown
        .dropdownBtn
        div
          img(src='/static/img/vipabc/headerBtn_2.png',srcset='/static/img/vipabc/headerBtn_2@2x.png 2x')
      .dropdown(v-on:click="openDropdown")
        .dropdownBtn
        div
          img(src='/static/img/vipabc/headerBtn_3.png',srcset='/static/img/vipabc/headerBtn_3@2x.png 2x')
        ul.dropdownMenu
          .dropdownMenuArrow
          .dropdownMenuTitle 设置
          li 字体大小
            img.i-arrow(src='/static/img/vipabc/icon_arrowForSubMenu.png',srcset='/static/img/vipabc/icon_arrowForSubMenu@2x.png 2x')
            ul.submenu.left
              li 小字體
              li 中字體
              li 大字體
          li 语言设定
            img.i-arrow(src='/static/img/vipabc/icon_arrowForSubMenu.png',srcset='/static/img/vipabc/icon_arrowForSubMenu@2x.png 2x')
            ul.submenu.left
              li 繁體中文
              li 簡體中文
              li 日文
              li 英語
          li 工具
            img.i-arrow(src='/static/img/vipabc/icon_arrowForSubMenu.png',srcset='/static/img/vipabc/icon_arrowForSubMenu@2x.png 2x')
            ul.submenu.left
              li 音量控制
              li 互聯網帶寬使用統計表
              li 下載遠端連線軟件
                img.i-arrow(src='/static/img/vipabc/icon_arrowForSubMenu.png',srcset='/static/img/vipabc/icon_arrowForSubMenu@2x.png 2x')
                ul.submenu.left
                  li Windows 版
                  li Mac 版
              li 話筒設置
              li 攝像頭設置
          li 刷新教室
          .dropdownMenuFooter
            .samll Copyright 2016 iTutorGroup
      .roomNumber
        .class-num {{roomNum}}
</template>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .headerInner{
    display: block;
    position: relative;
    width: 100%;
    padding: 6px 15px;
    padding-top: 10px;
    background: #4B4D50;
    height: 55px;
    .headerLogo{
      float: left;
      width: 210px;
    }
    .statusBtnGroup{
      display: inline-block;
      position: relative;
      float: left;
      margin-left: 20px;
      width: auto;
      height: 100%;
      .dropdownMenu{
        position: absolute;
        left:-15px;
      }
    }
    .headerBtnGroup{
      float: right;
      margin-right: 20px;
      .dropdownMenu{
        position: absolute;
        left:-55px;
      }
      .roomNumber{
        position: relative;
        display: inline-block;
