          position: absolute;
          top: 0;
          left: 0;
          color: #fff;
        }
      }
    }
    .dropdown{
      display: inline-block;
      position: relative;
      float: left;
      margin-right: 10px;
    }
    .dropdownBtn{
      position: absolute;
      cursor: pointer;
      display: block;
      vertical-align: middle;
      width: 100%;
      height: 100%;
      background: rgba(255,255,2550,0);
      z-index: 2;
    }
    .dropdownIcon{
      display: inline-block;;
      vertical-align: middle;
      text-align: left;
      font-size: 14px;
      height: 28px;
      line-height: 26px;
      border-radius: 28px;
      padding: 0 40px;
      background: #F1293B;
      color: #fff;
      border: 2px solid #FF6B6B;
      z-index: 1;
      .star{
        cursor: pointer;
        position: absolute;
        left: -10px;
        top: -2px;
      }
      .user{
        cursor: pointer;
        width: 40px;
        height: 40px;
        border-radius: 36px;
        border: 2px solid #FF6B6B;
        position: absolute;
        left: -10px;
        top: -6px;
      }
    }
    .dropdownMenu{
      position: absolute;
      top: 50px;
      left: -50%;
      color: #333;
      width: auto;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0px 0px 4px 1px #cecdcd;
      box-sizing: border-box;
      z-index: 9999;
      display: none;
      min-width: 150px;
      .dropdownMenuTitle{
        width: 100%;
