        color: #A9A9A9;
        .samll{
          width: 108%;
          font-size: 10px;
          display:inline-block;
          -webkit-transform:scale(0.75);
        }
      }
      li{
        font-size: 12px;
        padding: 12px 10px;
        position: relative;
        &:hover{
          background: #eee;
          > .submenu{
            visibility: visible;
            transition: 0s;
          }
        }
        .i-arrow{
          width: 6px;
          position: absolute;
          top: 0;
          bottom: 0;
          margin: auto;
          right: 13px;
        }
        .left{
          left: -150px;
        }
        .right{
          left: 150px;
        }
        > .submenu{
          display: block;
          visibility: hidden;
          position: absolute;
          color: #333;
          width: auto;
          border-radius: 6px;
          background: #fff;
          box-shadow: 0px 0px 4px 1px #cecdcd;
          box-sizing: border-box;
          z-index: 9999;
          min-width: 150px;
          padding: 10px 5px;
          margin-top: -30px;
          transition: .1s .1s;
          .dropdownMenuTitle{
            width: 100%;
            font-size: 12px;
            color: #484848;
            border-bottom: 1px solid #ededed;
            padding: 10px;
            text-align: center;
          }
          li{
            font-size: 12px;
            padding: 12px 10px;
            &:hover{
              background: #eee;
            }
          }
          .headerTextarea {
            background: #F9F9F9;
            border: navajowhite;
            height: 70px;
            width: 100%;
            padding: 5px 10px;
            box-sizing: border-box;
            border: 1px solid #ededed;
            outline: 0;
            resize:none;
          }
          .sendBtn{
            text-align: center;
            margin-top: 5px;
            cursor: pointer;
          }

        }
      }
    }
    .show{
      .dropdownMenu{
        display: block !important;
      }
    }
  }
</style>



