<script>
export default {
  name: 'MemberTableItem',
  props: ['name','headsetVolume','microphoneOpen'],
  data () {
    return {
      value: 0,
      isMicrophoneOpen:true
    }
  },
  created: function () {
    this.setValue();
  },
  methods: {
    setValue: function(){
      var self = this
      self.value=this.headsetVolume
      self.isMicrophoneOpen=this.microphoneOpen
    }
  }
}
</script>
<template lang="jade">
  tr
    td.memberName
      .memberNameDiv {{ name }}
    td.memberMicrophone
      span.memberMicrophoneBtn
        img(v-if="isMicrophoneOpen==true",v-on:click="isMicrophoneOpen=false",src='/static/img/vjr/icon_microphoneOpen.png',srcset='/static/img/vjr/icon_microphoneOpen@2x.png 2x')
        img(v-if="isMicrophoneOpen==false",v-on:click="isMicrophoneOpen=true",src='/static/img/vjr/icon_microphoneClose.png',srcset='/static/img/vjr/icon_microphoneClose@2x.png 2x')
    td.memberHeadsetRange
      input(type='range', v-model="value" , step='5', min='0', max='100')
</template>
<style lang='scss' scoped>
.memberName{
  width: 40%;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: left;
  border-left: 1px solid #FFEED6;
  border-right: 1px solid #FFEED6;
  vertical-align: middle;
  .memberNameDiv{
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
.memberMicrophone{
  width: 15%;
  text-align: center;
  border-left: 1px solid #FFEED6;
  border-right: 1px solid #FFEED6;
  vertical-align: middle;
  .memberMicrophoneBtn{
    cursor: pointer;
  }
}
.memberHeadsetRange{
  width: 40%;
  text-align: center;
  padding: 0 10px;
  box-sizing: border-box;
  border-left: 1px solid #FFEED6;
  border-right: 1px solid #FFEED6;
  vertical-align: middle;
}
input[type=range] {
    margin: auto;
    outline: none;
    padding: 0;
    width: 90%;
    height: 2px;
    background-color: #feebc5;
    background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, #f74c4c), color-stop(100%, #f74c4c));
    background-image: -webkit-linear-gradient(#f74c4c, #f74c4c);
    background-image: -moz-linear-gradient(#f74c4c, #f74c4c);
    background-image: -o-linear-gradient(#f74c4c, #f74c4c);
    background-image: linear-gradient(#f74c4c, #f74c4c);
    background-size: 50% 100%;
    background-repeat: no-repeat;
    border-radius: 10px;
    cursor: pointer;
    -webkit-appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
    box-shadow: none;
    border: none;
    background: transparent;
    -webkit-appearance: none;
}

input[type=range]::-moz-range-track {
    box-shadow: none;
    border: none;
    background: transparent;
}

input[type=range]::-moz-focus-outer {
    border: 0;
}

input[type=range]::-webkit-slider-thumb {
    width: 10px;
    height: 10px;
    border: 0;
    background: #f74c4c;
    border-radius: 100%;
    box-shadow: 0 0 1px 0px rgba(0,0,0,0.1);
    -webkit-appearance: none;
}

input[type=range]::-moz-range-thumb {
    width: 14px;
    height: 14px;
    border: 0;
    background: #f74c4c;
    border-radius: 100%;
    box-shadow: 0 0 1px 0px rgba(0,0,0,0.1);
}
</style>



