<script>
export default {
  name: 'MaterialDownloadModal',
  props: [],
  components: {
  },
  methods: {
    hideMaterial: function () {
      this.$emit('rootHideMaterial')
    }
  }
}
</script>
<template lang="jade">
  .MaterialDownloadModal
    .mask-download-pdf
      .alertIcon
        img(src='/static/img/vjr/alert_icon.png',srcset='/static/img/vjr/alert_icon@2x.png 2x')
      .downloadMessage
        | 确认下载第6页教材？
        br
        | 文件大小2.7M
      .buttonGroup
        .btn.cancel(v-on:click="hideMaterial")
          img(src='/static/img/vjr/icon_modalCancelBtn.png',srcset='/static/img/vjr/icon_modalCancelBtn@2x.png 2x')
        .btn.download
          img(src='/static/img/vjr/icon_modalDownloadBtn.png',srcset='/static/img/vjr/icon_modalDownloadBtn@2x.png 2x')

</template>
<style lang='scss' scoped>
  @import "../../assets/scss/mixins.scss";
  .MaterialDownloadModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 600;
  .mask-download-pdf {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 408px;
    height: 235px;
    background: #fff;
    border: 4px solid #eee;
    border-radius: 16px;
    padding: 60px 0 30px;
    box-sizing: border-box;
  }
  .alertIcon {
    position: absolute;
    text-align: center;
    top: -120px;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
  }

  .downloadMessage {
    color: #484848;
    font-size: 20px;
    text-align: center;
    line-height: 30px;
  }

  .buttonGroup {
    text-align: center;
    margin: 27px 0 0;
  }

  .btn {
    display: inline-block;;
    width: 140px;
    margin-right: 20px;
    cursor: pointer;
  }
}
</style>



