<script>
import WhiteboardHeader from 'components/Vipabc/WhiteboardHeader'
import WhiteboardBody from 'components/Vipabc/WhiteboardBody'
import StudentVideoList from 'components/Vipabc/StudentVideoList'
export default {
  name: 'Whiteboard',
  props: ['StudentVideoListData','MaterialDownloaderShow'],
  components: {
    WhiteboardHeader,
    WhiteboardBody,
    StudentVideoList
  },
  methods: {
    showMaterial: function () {
      this.$emit('rootShowMaterial')
    }
  }
}
</script>
<template lang="jade">
  .Whiteboard
    WhiteboardHeader(,v-on:showMaterial="showMaterial",:MaterialDownloaderShow='MaterialDownloaderShow')
    WhiteboardBody
    StudentVideoList(:StudentVideoListData='StudentVideoListData')
</template>
<style lang='scss' scoped>
  @import '../../assets/scss/mixins.scss';
  .Whiteboard {
    @include flex();
    @include flex-width(1);
    flex-direction:column;
    overflow: hidden;
    position: relative;
  }
</style>



